<template>
    <div>
        <loader :isVisible="isLoading"></loader>
       <!-- start page heading -->
        <div class="col-md-12 p-0 my-3">
            <div class="theme-primary partition p-actions-expand">
                <div class="theme-primary partition">
                    <span class="p-name text-white">{{ $t('ManageLayoutTitle') }}: {{ ModuleName }} >> {{ $t(SubModuleName)
                    }}</span>
                    <a v-on:click=Backtolist($event) title="" id="backtolist" class="p-action-btn text-white float-right"
                        :data-original-title="$t('BacktoList')">
                        <em class="fa fa-angle-double-left pr-2"></em><br />{{ $t('BacktoList') }}
                    </a>
                </div>
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em></a>
                </span>
            </div>
        </div>
        <!-- start  system left panel -->
        <div class="left-menu-tab " v-if="PreviewMode != 'Preview'">
            <div class="col-md-12 p-0 ">
                <div class="theme-primary partition">
                    <span class="p-name text-white">{{ $t('SystemFields') }}</span>
                </div>
            </div>
            <div class="accordion md-accordion left-filter" id="accordionEx" role="tablist" aria-multiselectable="true">
                <div class="card-panel">
                    <div class="card pt-2">
                        <!-- Search box for fields -->
                        <div class="row mx-0">
                            <div class="col-md-8 px-2">
                                <input type="text" name="Search" class="form-control" placeholder="Search System layout"
                                    v-model="searchLayout">
                            </div>
                            <div class="col-md-4 px-1">
                                <a id="Search" href="javascript:;" title="" class="btn btn-success mr-2"
                                    data-toggle-tooltip="tooltip" data-original-title="Click here to search"
                                    v-on:click="filteredSystemField">
                                    <em class="fa fa-search"></em></a>
                                <a id="ResetFilters" href="javascript:;" title="" class="btn btn-danger"
                                    data-toggle-tooltip="tooltip" data-original-title="Click here to reset"
                                    v-on:click="RefreshSystemFieldList"><em class="fa fa-refresh"></em></a>
                            </div>
                        </div>
                        <!-- Left System fields  -->
                        <div id="collapseOne12" class="collapse show" role="tabpanel" aria-labelledby="headingOne12"
                            data-parent="#accordionEx">
                            <div class="card-body p-2">
                                <draggable v-model="FiledListLeft" element="ul" class="list-group ulparted pb-0"
                                    @start="drag = true" @end="endDrag($event, 'System_Layout')" item-key="custom_field_id"
                                    group="my-groupForFields" id="StopDragfield" :move="checkMove">
                                    <div class="list-group-item left-fields p-2 mb-2" v-bind:groupid="element.group_id"
                                        v-if="element.is_system_generated == true" v-bind:id="element.type"
                                        v-bind:title="element.name" :key="index" v-for="(element, index) in FiledListLeft">
                                        <label class="w-100 font-16 py-2 bg-white border px-3">{{
                                            $t(element.display_name !=
                                                null ? element.display_name : element.name) }}</label>
                                        <em class="fa fa-arrows" aria-hidden="true"></em>
                                    </div>
                                </draggable>
                            </div>
                        </div>
                        <!-- <button class="btn btn-primary" v-on:click="OpenAddCustomFieldPopup()">Add Custom
                            Field</button> -->
                    </div>
                </div>

            </div>


            <div class="col-md-12 p-0  mt-2">
                <div class="theme-primary partition">
                    <span class="p-name text-white">{{ $t('CustomFields') }}</span>
                </div>
            </div>

            <div class="accordion md-accordion left-filter" id="accordionEx" role="tablist" :disabled="!enabled"
                aria-multiselectable="true">
                <div class="card-panel">
                    <div class="card pt-2">
                        <!-- Search box for fields -->

                        <div class="row mx-0">
                            <div class="col-md-8 px-2">
                                <input type="text" name="Search" class="form-control" placeholder="Search Custom layout"
                                    v-model="searchCustomLayout">
                            </div>
                            <div class="col-md-4 px-1">
                                <a id="Searchingh" href="javascript:;" title="" class="btn btn-success mr-2"
                                    data-toggle-tooltip="tooltip" data-original-title="Click here to search"
                                    v-on:click="filteredCustomField">
                                    <em class="fa fa-search"></em></a>
                                <a id="ResetFilters" href="javascript:;" title="" class="btn btn-danger"
                                    data-toggle-tooltip="tooltip" data-original-title="Click here to reset"
                                    v-on:click="RefreshCustomFieldList"><em class="fa fa-refresh"></em></a>
                            </div>
                        </div>

                        <!-- Left custom fields  -->
                        <div id="collapseOne12" class="collapse show" role="tabpanel" aria-labelledby="headingOne12"
                            data-parent="#accordionEx">
                            <div class="card-body p-2">
                                <draggable v-model="CustomFieldListLeft" element="ul" class="list-group ulparted pb-0 "
                                    id="customID" @start="drag = true" @end="endDrag($event, 'Custom_Layout')"
                                    :move="checkMove" item-key="custom_field_id" group="my-groupForFields">
                                    <template v-for="(element, index) in CustomFieldListLeft">
                                        <div class="list-group-item left-fields p-2 mb-2" v-bind:groupid="element.group_id"
                                            v-if="element.is_system_generated == false" v-bind:id="element.type"
                                            v-bind:title="element.name" v-bind:key="index">
                                            <label class="w-100 font-16 py-2 bg-white border px-3">{{
                                                $t(element.display_name !=
                                                    null ? element.display_name : element.name) }}</label>
                                            <em class="fa fa-arrows" aria-hidden="true"></em>
                                        </div>
                                    </template>
                                </draggable>
                            </div>
                        </div>
                        <button class="btn btn-primary" v-on:click="OpenAddCustomFieldPopup()">Add Custom
                            Field</button>
                    </div>
                </div>
            </div>
        </div>
        <div v-bind:class="{ 'right-content': true, 'disabled': PreviewMode == 'Preview' }">
            <div class="listing">
                <div class="expand-list right-slide-arrow">
                    <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                        <em class="icon-solid arrow-solid-left c" style="font-size:9pt"></em>
                    </a>
                </div>
                <form>
                    <div class="listing">
                        <draggable v-model="LayoutList" :list="LayoutList" @start="drag = true" item-key="group_id"
                            group="my-groupForFields1" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                            <div v-for="(items, layoutIndex) in LayoutList" :key="layoutIndex"
                                class="collapse show float-left border mt-2">

                                <!-- Group name div start -->
                                <div class="mcf-head float-left border-bottom">
                                    <em class="fa fa-th-large float-left mt-3 mr-2 text-secondary" aria-hidden="true"></em>
                                    <input type="text" v-model.trim="items.group_display_name"
                                        v-bind:class="{ 'form-control form-control-sm': true, 'is-invalid': (isGroupNameBlank == items.group_name || duplcateGroupNameExists == items.group_display_name) }" />
                                    <div class="field-action mt-2 parentdiv" id="MainDivForLayout">
                                        <div class="dropdown">
                                            <a aria-hidden="true" @click="openmainDropdown($event)"><em
                                                                class="fa fa-cog action_icon_cv"></em></a>
                                            <div>
                                                <div class="verticle-action rt25 rt502">
                                                    <span class="action-list-box small filter-list-box">
                                                        <span id="action-list"
                                                            class="list-actions text-left p-0 datarep ml-3"
                                                            style="width:220px;">

                                                            <div class="modal-header ui-draggable-handle px-2 py-2">
                                                                <span class="text-center font-16">Select layout</span>
                                                                <a type="button" class="bg-transparent text-end"><em aria-hidden="true" class="fa fa-times text-dark"  @click="closemainDropdown($event)"></em></a>
                                                            </div>

                                                            <a class="dropdown-item px-2 py-2" href="javascript:;"
                                                                v-for="layouttype in LayoutTypeList">
                                                                <label class="form-check-inline menu_widjet">
                                                                    <input type="radio" class="chkitems"
                                                                        v-bind:value="layouttype.value"
                                                                        v-model="items.LayoutType" />
                                                                    <span class="label-text mx-1">{{ $t(layouttype.name)
                                                                    }}</span>
                                                                </label>
                                                            </a>
                                                            <a class="dropdown-item border-top py-2 pl-2 bg-light"
                                                                href="javascript:;" v-if="items.group_id <= 0 || (items.FieldData.length == 1 && items.FieldData[0].is_include_in_post_jon == 'no' ||
                                                                    (items.group_is_system_generated == false))"
                                                                v-on:click="RemoveLayout(items, items.group_id)">
                                                                <em class="fa fa-trash text-danger"
                                                                    style="font-size:20px;"></em>
                                                                <span class="ml-1 text-dark"
                                                                    style="font-size:14px;">Removelayout</span>
                                                            </a>
                                                        </span>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <draggable :list="items.FieldData" item-key="custom_field_id" @end="onDragAdd($event)"
                                    v-bind="$attrs" group="my-groupForFields" class="col-12 float-left p-0"
                                    @start="drag = true" :move="checkMove" v-bind:groupid="items.group_id"
                                    v-bind:groupName="items.group_display_name"
                                    v-bind:systemgenerated="items.group_is_system_generated"
                                    v-bind:fieldLength="items.FieldData.length" style="min-height:100px;">
                                    <div v-for="(element, elementIndex) in items.FieldData"
                                        v-bind:id="'FieldId' + element.custom_field_id"
                                        v-bind:class="{ 'col-sm-12 col-md-12 float-left p-2': true, 'col-lg-12 col-xl-12': items.LayoutType == 'single', 'col-lg-6 col-xl-6': items.LayoutType == 'double', 'col-lg-4 col-xl-4': items.LayoutType == 'triple', 'col-lg-3 col-xl-3': items.LayoutType == 'four' }"
                                        v-bind:key="elementIndex" v-bind:fielddata="element"
                                        v-show="element.is_include_in_post_jon == 'yes'"
                                        v-bind:systemgenerated="items.group_is_system_generated == false ? 0 : 1"
                                        v-bind:fieldLength="items.length">
                                        <div class="modal-header ui-draggable-handle  py-0">
                                            <button type="button" class="close bg-transparent"
                                                v-on:click="closeCustomField(element, items.FieldData)">
                                                <em aria-hidden="true" class="fa fa-times text-dark"></em>
                                            </button>
                                        </div>
                                        <div v-bind:class="{
                                            'pickflowbase ml-cutomf-bg float-left p-2 w-100': true, 'field-required': (element.is_required == true),
                                            'box-warning': element.is_system_generated == 0,
                                            'border-danger': (duplicateNameExists == element.name || picklistOptionBlankItems.includes(element.name))
                                        }">
                                            <!-- create custom fileds dynamically -->
                                            <div class="labeltext text-left p-0 float-left">
                                                <label v-bind:disabled="element.is_system_generated > 0"
                                                    v-bind:class="{ 'form-control form-control-sm': true, 'disabled': element.is_system_generated > 0 }"
                                                    v-if="element.is_system_generated == true">{{
                                                        $t(element.display_name != null ? element.display_name :
                                                            element.name) }}</label>
                                                <input type="text" v-model="element.display_name" v-bind:disabled="true"
                                                    v-bind:class="{ 'form-control form-control-sm': true, 'disabled': element.is_system_generated > 0 }"
                                                    v-if="element.is_system_generated == false"
                                                    v-on:keyup="ChangeFieldName(element)" />
                                                <label class="d-none">{{ element.name }}</label>
                                            </div>

                                            <div class="dropdown float-right pt-1 mr-3 parentdiv1" id="mainDivProp">

                                                <div class="dropdown">
                                                    <a aria-hidden="true" @click="openSystemgenrateddropdown($event)"><em
                                                                class="fa fa-cog action_icon_cv"></em></a>
                                                    <div>
                                                <div class="verticle-action1 rt25 rt502">
                                                    <span class="action-list-box small filter-list-box">
                                                        <span id="action-list"
                                                            class="list-actions text-left p-0 datarep ml-3 border-0"
                                                            style="width:220px; box-shadow: none;">

                                                            <div class="modal-header ui-draggable-handle  py-0 border-0">
                                                            <div class="dropdown-menu panelfix_dots rounded border bg-white d-block"
                                                               
                                                                v-bind:id="'FieldProId' + element.custom_field_id"
                                                                aria-labelledby="dropdownMenuLink">
                                                                <div class="modal-header ui-draggable-handle  py-0">
                                                                    <button type="button" class="close"
                                                                       >
                                                                        <em aria-hidden="true" class="fa fa-times" style="top: -14px;left: -6px;" @click="popupcloseforsystemfields"></em>
                                                                    </button>
                                                                </div>
                                                                <a class="dropdown-item px-2 py-2" href="javascript:;">
                                                                    <label class="form-check-inline">
                                                                        <input type="checkbox" v-bind:value="true"
                                                                            v-model="element.is_required"
                                                                            v-bind:disabled="element.is_system_generated > 0 && element.system_is_required == true" />
                                                                        <span class="label-text text-secondary mx-1"
                                                                            style="font-size:14px;">Mark As
                                                                            Required</span>
                                                                    </label>
                                                                </a>
                                                                <a class="dropdown-item px-2 py-2" href="javascript:;"
                                                                   :BindData="EditProperty"
                                                                    v-on:click="EditProperties(element)">
                                                                    <em class="fa fa-eye text-primary"
                                                                        style="font-size:20px;"></em>
                                                                    <span class="ml-1 text-secondary"
                                                                        style="font-size:14px;">{{
                                                                            (element.system_is_required
                                                                                == true && element.is_system_generated ==
                                                                                true) ? 'ViewProperties' : 'EditProperties'
                                                                        }}</span>
                                                                </a>
                                                                <div class="dropdown-divider m-0"></div>
                                                                <a class="dropdown-item px-2 py-2" href="javascript:;"
                                                                    v-on:click="closeCustomField(element, items.FieldData)"
                                                                    :custom_field_id="element.custom_field_id"
                                                                    :is_required="element.is_required"
                                                                    :is_updated="element.is_updated"
                                                                    v-if="element.system_is_required == false">
                                                                    <em class="fa fa-trash text-danger"
                                                                        style="font-size:20px;"></em>
                                                                    <span class="ml-1 text-dark"
                                                                        style="font-size:14px;">RemoveField</span>
                                                                </a>

                                                            </div>

                                                        </div>
                                                        </span>
                                                    </span>
                                                </div>
                                            </div>
                                                </div>
                                            </div>
                                            <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;"
                                                v-if="element.dt_code == 'radio'"
                                                v-bind:title="GetFieldType(element.dt_code, element.validation_type)">
                                                <em class="fa fa-dot-circle-o" style="font-size:18px;"></em>
                                            </div>
                                            <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;"
                                                v-if="element.dt_code == 'select'"
                                                v-bind:title="GetFieldType(element.dt_code, element.validation_type)">
                                                <em class="fa fa fa-list" style="font-size:20px;"></em><select
                                                    class="form-control form-control-sm d-none" style="width:50px;">
                                                    <option>Select</option>
                                                </select>
                                            </div>
                                            <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;"
                                                v-if="element.dt_code == 'date'"
                                                v-bind:title="GetFieldType(element.dt_code, element.validation_type)">
                                                <em class="fa fa-calendar-o" style="font-size:20px;"></em>
                                            </div>
                                            <div class="fieldtext float-right mt-2 mr-3" style="text-transform:capitalize;"
                                                v-if="(element.dt_code == 'int' || element.dt_code == 'bigint' || element.dt_code == 'decimal')"
                                                v-bind:title="GetFieldType(element.dt_code, element.validation_type)">
                                                <em class="fa fa-sort-numeric-asc" style="font-size:20px;"></em>
                                            </div>
                                        </div>
                                    </div>
                                </draggable>
                            </div>
                        </draggable>
                    </div>
                    <div class="col-lg-12 p-0 text-right mt-3 float-left">
                        <a href="javascript:;" class="btn btn-primary mr-2" v-on:click="AddNewGroup()" title="NewGroup"><em
                                class="fa fa-users pr-2"></em>NewGroup</a>
                        <a href="javascript:;"
                            v-bind:class="{ 'btn btn-success mr-2': true, 'disabled': (submitButtom == true) }"
                            v-on:click="SubmitData()" title="Save"><em class="fa fa-save pr-2"></em>Save</a>
                        <!-- <a href="javascript:;" class="btn btn-danger" title="Cancel"><em class="fa fa-close pr-2"
                                v-on:click="Cancel"></em>Cancel</a> -->
                                <button class="btn btn-primary delete-btn my-sm-0" :title="$t('Cancel')" v-on:click="Cancel">
                                            <em class="fa fa-times pl-2 pr-2" ></em>
                                            {{ $t("Cancel") }}
                                </button>
                    </div>
                </form>
            </div>
        </div>
        <OpenAddCustomFieldPopup v-if="showCustomfieldPopup" @GetCustomNewField="GetCustomNewField" :LanguageArrayData=LanguageArray :Sub_ModuleId=Sub_ModuleId 
            :ModuleId="ModuleId"  @hidePopup='hidePopup' ></OpenAddCustomFieldPopup>
        <CustomFieldPropertyPopup v-if="showFieldPropPopup" :divManageLayoutProp="divManageLayoutProp" :DataTypeId="DataTypeId"
            @closepopup='closepopup' :LanguageArray=LanguageArray :iSLayoutControl="iSLayoutControl" :Field="Field"
            :AddCustomFields="AddCustomFields" :oldFieldPropertyData="oldFieldPropertyData" :FieldName="FieldName" 
            :IsSystemGeneratedField="IsSystemGeneratedField" :EditProperty="EditProperty"></CustomFieldPropertyPopup>
    </div>
</template>
<script>
import draggable from 'vuedraggable';
import OpenAddCustomFieldPopup from '../components/Common/Modal/AddCustomFieldsPopup.vue';
import CustomFieldPropertyPopup from '../components/Common/Modal/CustomFieldPropertyPopup.vue'
import DataService from '../services/DataService.js';
export default {
    poprs: {
        CustomlayoutId: {
            type: Number,
            required: true
        },
        FieldPropertyData:{
            type: Array,
            required: true
        }
    },
    components: {
        "draggable": draggable,
        OpenAddCustomFieldPopup,
        CustomFieldPropertyPopup
    },
    data() {
        return {
            LayoutDetailsData: [],
            FiledList: [],
            FiledListLeft: [],
            CustomFieldListLeft: [],
            otherData: '',
            langCode: 'en',
            moduleName: '',
            subModuleCode: '',
            CustomLayout_Id: '',
            id: '',
            FieldData: null,
            LayoutList: [],
            LayoutTypeList: [{ name: "DoubleColumn", value: "double" },
            { name: "TripleColumn", value: "triple" },
            { name: "FourColumn", value: "four" }],
            FieldActionList: [{ name: "Mark as required", value: true },
            { name: "Edit Properties", value: "editprop" },
            { name: "Remove Field", value: "remove" }],
            showModal: false,
            EditProperty: null,
            nonDisplayField: [],
            addGroupId: 1,
            duplicateNameExists: '',
            duplcateGroupNameExists: '',
            isGroupNameBlank: '',
            LanguageArray: [],
            LanguageArrayData: [],
            submitButtom: false,
            picklistOptionBlankItems: [],
            drag: false,
            showCustomfieldPopup: false,
            FieldLayoutData: [],
            OldIdForProp: '',
            OldLayoutIconVal: '',
            showFieldPropPopup: false,
            FieldName: '',
            Field: '',
            iSLayoutControl: false,
            divManageLayoutProp: false,
            enabled: true,
            RemovefieldsPopup: '',
            AddCustomFields: '',
            ModuleId: '',
            Sub_ModuleId: '',
            oldFieldPropertyData: [],
            searchLayout: '',
            searchCustomLayout: '',
            jsonFilteredData: [],
            CustomFieldjsonData: [],
            LayoutFilterData: [],
            FieldPropertyData: [],
            IsSystemGeneratedField: '',
            ModuleName: '',
            SubModuleName: '',
            isLoading: false,
            PreviewMode: '',
            disabled: false,
            DataTypeId:''
        }
    },
    created: function () {
        var vm = this;
        vm.CustomLayout_Id = vm.$route.params.CustomlayoutId;
        vm.PreviewMode = vm.$route.params.PreviewMode
        vm.FetchLayoutData(vm.CustomLayout_Id);

    }, 
    methods: {
        hidePopup() {  
        this.showCustomfieldPopup = false   
        },
        openmainDropdown: function (event) {
            $(".filter-list-box").width('0px').css("overflow", "hidden");
            $(event.target).closest(".parentdiv").find(".filter-list-box").css("overflow", "visible");
        },
        closemainDropdown: function (event) {
            $(".filter-list-box").width('0px').css("overflow", "hidden");
        },
        openSystemgenrateddropdown: function (event) {
            $(".filter-list-box").width('0px').css("overflow", "hidden");
            $(event.target).closest(".parentdiv1").find(".filter-list-box").css("overflow", "visible");
        },
        popupcloseforsystemfields: function(){
            $(".filter-list-box").width('0px').css("overflow", "hidden");    
        },
        //this method for searching layout left side.
        filteredSystemField: function () {
            var vm = this
            if (vm.searchLayout != "") {
                let CheckIfExist = vm.jsonFilteredData.filter(usr => usr.display_name.toLowerCase().includes(vm.searchLayout));
                vm.FiledListLeft = CheckIfExist;
            }
            else {
                vm.RefreshSystemFieldList()
            }
        },
        filteredCustomField: function () {
            var vm = this
            if (vm.searchCustomLayout != "") {
                let CheckIfExist = vm.CustomFieldjsonData.filter(usr => usr.display_name.toLowerCase().includes(vm.searchCustomLayout));
                vm.CustomFieldListLeft = CheckIfExist;
            }
            else {
                vm.RefreshCustomFieldList()
            }
        },
        // this method for reset left  side SystemField layout list.
        RefreshSystemFieldList: function () {
            var vm = this;
            vm.searchLayout = '';
            vm.FiledListLeft = vm.jsonFilteredData;
        },
        // this method for reset left  side CustomField layout list.
        RefreshCustomFieldList: function () {
            var vm = this;
            vm.searchCustomLayout = '';
            vm.CustomFieldListLeft = vm.CustomFieldjsonData;
        },
        // this method for remove user genrated layout.
        RemoveLayout: function (e, id) {
            var vm = this;
            var fullLayoutList = vm.LayoutList;
            vm.confirmR(vm.$t("ConfirmDeleteGroup"), true, (vm.$t("Remove") + "  " + vm.$t("CustomFiled")), function () {
                var chkGroup = $.grep(fullLayoutList, function (element, index) {
                    return element.group_id != id;
                });
                vm.LayoutList = chkGroup;
            })
        },
        closepopup: function () {
            this.showFieldPropPopup = false;
        },
        // Remove CustomFields POpup 
        CloseRemovefieldsPopup: function () {
            this.RemovefieldsPopup = false;
        },
        // RemoveField property of custom field controls and push left side 
        closeCustomField: function (model, arr) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("CustomFiled")), function () {
                var checkindex = arr.findIndex(x => x.custom_field_id === model.custom_field_id);
                vm.FiledListLeft.push({ ...model });
                vm.jsonFilteredData.push({ ...model });
                vm.CustomFieldListLeft.push({ ...model });
                vm.CustomFieldjsonData.push({ ...model });
                arr.splice(checkindex, 1);
            });
        },
        Backtolist: function () {
            var vm = this;
            vm.$router.push({ name: 'CustomLayoutListing' });
        },
        // Edit property of custom field controls
        EditProperties(field) {
            var vm = this;
            vm.AddCustomFields = 'FromManage'
            vm.Field = field;
            vm.IsSystemGeneratedField = field.is_system_generated;
            vm.iSLayoutControl = false;
            vm.divManageLayoutProp = true;
            vm.FieldName = field.display_name;
            vm.oldFieldPropertyData = vm.FieldPropertyData;
            vm.LanguageArray = vm.LanguageArray;
            vm.EditProperty = field;
            vm.DataTypeId=field.data_type_id
            vm.showFieldPropPopup = true;
        },
        OpenAddCustomFieldPopup() {
            var vm = this;
            vm.ModuleId = vm.LayoutDetailsData.module_id;
            vm.Sub_ModuleId = vm.LayoutDetailsData.sub_module_id;
            vm.LanguageArrayData = vm.LanguageArray;
            vm.showCustomfieldPopup = true;
        },
        // CloseAddCustomFieldPoup() {
        //     var vm = this;
        //     vm.showCustomfieldPopup = false;
        //     vm.FetchLayoutData();
        // },
        onDragAdd(evt) {
            if (evt.to.id == 'StopDragfield') {
                this.jsonFilteredData.push(evt.from.__vue__.$attrs.groupid);
            }
            if (evt.clone.id == null) {
                if (evt.clone.id == "new" && typeof (evt.target.getAttribute("groupid")) != undefined && evt.target.getAttribute("groupid") != null) {
                    return false;
                }
            }
            this.updateEvenemt(evt);
            var element = evt.item._underlying_vm_;
            console.log('element2222', element);
            //var lableName = evt.item.innerText;
            if (element === undefined) {
                return;
            }
            var newIndex = evt.newIndex;
            if (element.custom_field_id > 0) {
                element.group_id = evt.from.__vue__.$attrs.groupid;
                element.is_updated = 1;
            }
            element.CUSTOM_LAYOUT_ID = this.CustomlayoutId;
            this.spliceList(newIndex, 0, element);
            var added = { element: element, newIndex: newIndex };
            this.emitChanges({ added: added });

        },
        emitChanges(evt) {
            var _this5 = this;
            this.$nextTick(function () {
                _this5.$emit('change', evt);
            });
        },
        updateEvenemt: function updateEvenemt(evt) {
            console.log('updateEvenemt');
            this.updateProperty(evt, 'newIndex');
            this.updateProperty(evt, 'oldIndex');
        },
        updateProperty: function updateProperty(evt, propertyName) {
            evt.hasOwnProperty(propertyName) && (evt[propertyName] += 0);
        },
        spliceList() {
            var _arguments = arguments;
            var spliceList = function spliceList(list) {
                return list.splice.apply(list, _arguments);
            };
        },
        FetchLayoutData() {
            var vm = this;
            vm.isLoading = true;
            var param = `id=&moduleName=&subModuleCode=&otherData=${vm.otherData}&langCode=${vm.langCode}&CustomLayoutId=${vm.CustomLayout_Id}&usefor='data'`;
            DataService.ManageFormLayout(param).then(function (response) {
                if (response.data.data != undefined) {
                    vm.FieldData = response.data.data;
                    vm.LayoutDetailsData = response.data.LAYOUTDETAILS[0];
                    vm.LanguageArray = response.data.languages;
                    vm.nonDisplayField = vm.FieldData.filter((e) => e.form_field_visibility == 'NO');
                    var fieldData = vm.FieldData.filter((e) => e.form_field_visibility == 'YES' && e.display_order != null);
                    vm.FiledListLeft = vm.FieldData.filter((e) => e.form_field_visibility == 'YES' && e.display_order == null && e.is_system_generated == true);
                    vm.CustomFieldListLeft = vm.FieldData.filter((e) => e.form_field_visibility == 'YES' && e.display_order == null && e.is_system_generated == false);
                    vm.jsonFilteredData = vm.FieldData.filter((e) => e.form_field_visibility == 'YES' && e.display_order == null && e.is_system_generated == true);
                    vm.CustomFieldjsonData = vm.FieldData.filter((e) => e.form_field_visibility == 'YES' && e.display_order == null && e.is_system_generated == false);
                    vm.FiledList = fieldData;
                    vm.FieldData = fieldData;
                    var FistDataObject = fieldData[0];
                    var newLayoutIndex = vm.FiledList.findIndex(function (item, i) {
                        return item.group_id == '-1';
                    });
                    var getfields = vm.FiledList;
                    getfields.fieldData = FistDataObject;
                    getfields.fieldData.is_required = false;
                    vm.ModuleName = vm.FieldData[0].Module_name;
                    vm.SubModuleName = vm.FieldData[0].sub_module_code;
                    vm.FiledList[newLayoutIndex] = getfields;
                    if (vm.FieldData != null && vm.FieldData.length > 0) {
                        var groupId = vm.FieldData[0].group_id;
                        $.each(vm.FieldData, $.proxy(function (index, item) {
                            groupId = item.group_id;
                            item.cultureDetails = vm.MakeNormalArray(item.cultureDetails);
                            var chkGroup = vm.LayoutList.filter((e) => e.group_id == groupId);
                            if (chkGroup.length == 0) {
                                var returnedData = vm.FieldData.filter((e) => e.group_id == groupId);
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LayoutType: returnedData[0].layout_type,
                                        group_display_name: vm.GetLocalizedValue(returnedData[0].group_name, returnedData[0].group_display_name),
                                        group_is_system_generated: returnedData[0].group_is_system_generated,
                                        FieldData: returnedData
                                    };
                                    vm.LayoutList.push(objGroup);
                                }
                            }
                        }, vm));
                    }
                }
                vm.isLoading = false;
            })
        },
        MakeNormalArray(value) {
            try {
                return JSON.parse(value)
            }
            catch (ex) {
                return value
            }
        },
        AddNewGroup: function () {
            var groupId = this.FieldData[0].group_id;
            var returnedData = $.grep(this.FieldData, function (element, index) {
                return element.group_id == groupId;
            });
            var lastObject = returnedData[0];
            lastObject = Object.assign({}, lastObject, {});
            lastObject.is_include_in_post_jon = 'no';
            lastObject.name = "X" + this.addGroupId;
            lastObject.display_name = "X" + this.addGroupId;
            var fieldData = [];
            fieldData.push(lastObject);
            var objGroup = {
                group_id: -this.addGroupId,
                group_name: "NewGroup" + " " + this.addGroupId,
                group_display_name: "NewGroup" + " " + this.addGroupId,
                group_is_system_generated: false,
                LayoutType: 'double',
                FieldData: fieldData
            };
            this.LayoutList.push(objGroup);
            this.addGroupId += 1;
        },
        GetFieldType: function (name, type) {
            if (type == "email") {
                return type;
            }
            else {
                return name;
            }
        },
        GetLocalizedValue: function (fieldName, displayName) {
            if (this.isBlank(displayName)) {
                var chk = String(fieldName).toUpperCase();
                if (chk || /^\s*$/.test(chk) == true) {
                    return chk;
                }
                chk = String(fieldName);
                if (chk || /^\s*$/.test(chk) == true) {
                    return chk;
                }
                return fieldName;
            }
            else {
                var chk = String(displayName).toUpperCase();
                if (chk || /^\s*$/.test(chk) == true) {
                    return chk;
                }
                chk = String(displayName);
                if (chk || /^\s*$/.test(chk) == true) {
                    return chk;
                }
                return displayName;
            }
        },
        // Drag Move 
        checkMove: function (evt) {
            if (evt.draggedContext.element.is_system_generated && evt.to.id == 'StopDragfield') {
                return true;
            }
            if (evt.related.id != '') {
                if (evt.related.id == 'FieldId' + evt.relatedContext.element.custom_field_id) {
                    return true;
                }
            }
            else if (evt.dragged.id != '') {
                if (!evt.draggedContext.element.is_system_generated && evt.to.id == 'customID') {
                    return true;
                }
                // else if(evt.dragged.id == 'FieldId'+ evt.dragged._underlying_vm_.custom_field_id ) {
                //     return true;
                // }
                else if (evt.to.id == '') {
                    return true;
                }
                else {
                    return false;
                }
            }
            // else if(!evt.draggedContext.element.is_system_generated && evt.to.id == 'customID'){
            //     return true;
            // }
            else if (evt.to.id == '') {
                return true;
            }
            else {
                return false;
            }

        },
        // Drag will not move
        endDrag: function (e, lbl) {
            this.drag = true
            let item = e.item._underlying_vm_;
            if (lbl == 'System_Layout') {
                var index;
                this.jsonFilteredData.forEach((x, y) => {
                    if (x.custom_field_id == item.custom_field_id) {
                        index = y;
                    }
                })
                this.jsonFilteredData.splice(index, 1);
            }
            if (lbl == 'Custom_Layout') {
                var index;
                this.CustomFieldjsonData.forEach((x, y) => {
                    if (x.custom_field_id == item.custom_field_id) {
                        index = y;
                    }
                })
                this.CustomFieldjsonData.splice(index, 1);
            }
            if (e.item.id == 'StopDragfield' && !e.item._underlying_vm_.element.is_system_generated) {
                return true;
            }
            else if (e.to.id == 'customID') {
                return false;
            }
            else {
                e.preventDefault();
            }
        },
        // submit field layout data
        SubmitData: function () {
            this.submitButtom = true;
            var vm = this;
            vm.isLoading = true;
            var fullJson = this.LayoutList;
            var mainJson = [];
            var arrayNameField = [];
            var arrayGroupNameField = []
            this.picklistOptionBlankItems = []
            this.duplcateGroupNameExists = '';
            this.duplicateNameExists = '';
            this.isGroupNameBlank = '';
            $.each(this.LayoutList, function (index, element) {
                if (arrayGroupNameField.includes(element.group_display_name) == true) {
                    vm.duplcateGroupNameExists = element.group_display_name;
                } else {
                    arrayGroupNameField.push(element.group_display_name);
                }
                if (element.group_display_name == '') {
                    vm.isGroupNameBlank = element.group_name;
                }
                $.each(element.FieldData, function (index1, tempItem) {
                    if ((tempItem.dt_code == 'select' || tempItem.dt_code == 'radio' || tempItem.dt_code == 'checkbox') && $.trim(tempItem.picklist_options) == '') {
                        vm.picklistOptionBlankItems.push(tempItem.name);
                    }
                    if (arrayNameField.includes(tempItem.name) == true) {
                        vm.duplicateNameExists = tempItem.name;
                    } else {
                        arrayNameField.push(tempItem.name);
                    }
                });
            });
            $.each(fullJson, function (itr, item) {
                var fieldData = item.FieldData;
                var isUpdated = 0;
                if (typeof (fieldData) != undefined && fieldData != null) {
                    fieldData.forEach((itm, indx) => {
                        if (itm.custom_layout_id == null) {
                            itm.is_inserted = 1;
                            itm.is_updated = 0;
                        }
                        else {
                            itm.is_inserted = 0;
                            itm.is_updated = 1;
                        }
                    });
                }
                var fieldDataList = $.grep(fieldData, function (element, index) {
                    element.display_order = index + 1;
                    return (element.is_include_in_post_jon == 'yes');
                });
                var groupObject = {
                    group_id: item.group_id,
                    group_name: item.group_name,
                    group_display_name: item.group_display_name,
                    layout_type: item.LayoutType,
                    is_inserted: item.group_id <= 0 ? 1 : 0,
                    is_updated: isUpdated,
                    display_order: itr + 1,
                    FieldData: fieldDataList
                };
                mainJson.push(groupObject);
            });
            var postObject =
            {
                moduleName: vm.FieldData[0].Module_name,
                subModuleCode: vm.FieldData[0].sub_module_code,
                CUSTOM_LAYOUT_ID: vm.CustomLayout_Id,
                data: mainJson
            }
            if (this.duplicateNameExists == '' && this.duplcateGroupNameExists == '' && this.isGroupNameBlank == '' && this.picklistOptionBlankItems.length == 0) {
                var objectPostString = JSON.stringify(postObject);
                DataService.ManageLayoutPage(objectPostString).then(function (response) {
                    if (response.data[0].Status == 'Success') {
                        vm.ShowAlert(vm.$t('CustomLayoutSuccess', [vm.$t('Lauout')]), "success", true, vm.$t("Alert"));
                        vm.$router.push({ name: 'CustomLayoutListing' });
                    }
                    else if (response.data[0].Status == 'Failure') {
                        vm.ShowAlert(vm.$t('CustomLayoutError', [vm.$t('Lauout')]), "faliure", true, vm.$t("Alert"));
                    }
                })
                this.submitButtom = false;
                vm.isLoading = false;
            } else {
                vm.isLoading = false;
                this.submitButtom = false;
                alert('CorrectHighlightedErrors');
            }
        },
        Cancel() {
            var vm = this;
            vm.$router.push({ name: 'CustomLayoutListing' });
        },
        closePropPopup(myObj) {
            var vm = this;
            if (myObj != null) {
                vm.FieldPropertyData = myObj;
            }
            vm.showFieldPropPopup = false;
        },
        GetCustomNewField(fieldvalue){
            var vm = this;
             vm.CustomFieldListLeft.push(fieldvalue[0]);
        }
    }
};
</script>
